@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);
a {
  color: rgba(0, 0, 0, 65);
}

.card {
  background-color: white;
  display: flex;
  box-sizing: border-box;
  height: auto;
  border-radius: 4px;
  border: 1px solid #edeefa;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px 10px 10px 0;
  align-items: center;
}

.card:hover {
  box-shadow: 4px 10px 22px -18px rgba(45, 65, 240, 0.15);
}

.icon-block {
  text-align: center;
  width: 30px;
}

.info-block {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 20px;
  margin-right: 20px;
}

.tag-block {
  margin-left: auto;
  margin-right: 5px;
}

.menu-ellipses {
  margin-left: auto;
}

.ant-divider-horizontal {
  margin: 5px 0;
}

.tree-title {
  display: flex;
  align-items: center;
}

.light {
  color: #939394;
  margin-left: 15px;
  margin-bottom: 0.5em;
}

.card-divider {
  display: block;
  clear: both;
  width: 100%;
  min-width: 100%;
  height: 1px;
  margin-top: 5px;
  background: #e1e1e2;
}

.card-link {
  display: flex;
  align-items: center;
  flex-grow: 1;
  padding: 15px 0 15px 20px;
}

.card-link:hover {
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .card-link {
    padding: 5px 0 5px 20px;
  }

  .card .tree-title {
    font-size: 12px;
  }

  .card-timestamp {
    font-size: 11px;
  }

  .card .info-block {
    margin-right: 0;
  }
}

.skeleton {
  background-color: white;
  display: flex;
  height: 90px;
  border-radius: 4px;
  border: 1px solid #edeefa;
  padding: 10px 15px;
  width: 100%;
  margin: 20px 0 60px 0;
}

.skeleton-no-border {
  display: flex;
  height: 90px;
  padding: 10px 15px;
  width: 100%;
  margin: 20px 0 60px 0;
}

.skeleton li, .skeleton-no-border li {
  list-style: none;
  background: linear-gradient(90deg, #f2f2f2 25%, #e6e6e6 37%, #f2f2f2 63%);
  background-size: 400% 100%;
  height: 16px;
  width: 100%;
  margin-top: 13px;
  -webkit-animation: ant-skeleton-loading 1.4s ease infinite;
          animation: ant-skeleton-loading 1.4s ease infinite;
}

.skeleton-heading {
  width: 35% !important;
}

.skeleton ul, .skeleton-no-border ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

.halfWidthContainer {
  margin: auto;
  width: 50%;
  padding: 20px;
  padding-right: 30px;
  text-align: left;
}

.parentContainer {
  background-color: #F5F5F5;
  min-height: calc(100vh - 100px);
}

h2 {
  display: inline-block;
}

.no-trees {
  margin-bottom: 50px;
  color: #cacacb;
}

.tree-form .ant-time-picker-icon {
  right: -210px;
}

.tree-form .ant-form-item-children input {
  height: 40px;
  font-size: 15px;
}

@media screen and (max-width: 767px) {
  .halfWidthContainer {
    width: 100%;
  }

  .buttonBar {
    height: 60px;
  }

  .tree-form .ant-time-picker-icon {
    right: -110px;
  }

  .tree-form .ant-form-item-control-wrapper input {
    width: 250px;
  }
}

.progIcon {
    height: 32px;
    width: 32px;
    margin: 10px;
}

.progText {
    margin-bottom: 0px;
}

.progWrapper {
    display: flex;
    align-items: center;
}

.progContainer {
    display: flex;
    justify-content: space-between;
}

* {box-sizing:border-box}

/* Container for skill bars */
.progBarContainer {
  width: 100%; /* Full width */
  background-color: #ddd; /* Grey background */
  margin-top: 10px;
}

.progBar {
  text-align: right; /* Right-align text */
  padding-top: 10px; /* Add top padding */
  padding-bottom: 10px; /* Add bottom padding */
  padding-right: 10px;
  color: white; /* White text color */
}

.progBarDone {background-color: rgb(89, 208, 143);} /* Green */

.centreItems {
    text-align: center;
}
.react-tooltip-lite {
    background: #333;
    color: white;
    border-radius: 5px 5px 5px 5px;
  }
  
.react-tooltip-lite-arrow {
    border-color: #333;
}

.treeToolTip {
    height: 30px;
    
}

.queueToolTip {
    margin-top: 20px;
    padding-top: 0px;
    height: 10px;
    margin-bottom: 40px;

}

.beautifyToolTip {
    height: 30px;
    
}

.deleteToolTip {
    height: 70px;
}

.noSelectDelete {
    height: 70px;
}

.editToolTip {
    height: 30px; 
}

.chatBot {
    position: relative;
}
