.header-wrap {
  align-items: center;
  height: 120px;
  justify-content: space-around;
  display: flex;
  color: #F5F5F5;
  background: rgb(216, 213, 213);
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100; /* this is optional and should be different for every tree */
}

.logo img {
  height: 65px;
}

.logoWrap {
  position: absolute;
  left: 25px;
}

.rhs-buttons {
  color: white;
  display: inline-flex;
  position: absolute;
  right: 10px;
}

.rhs-buttons button {
  color: white;
  margin-right: 20px;
}

.searchButton {
  background-image: url(/static/media/magGlassTrans.ae3c363b.png);
  background-repeat: no-repeat;
  background-size: 85% 85%;
  background-position: center;
  background-color: rgb(216, 213, 213);
  height: 30px;
  width: 30px;
  border-radius: 15px;
  box-shadow: none;
}

.searchButton:hover {
  opacity: 0.6;
  cursor: pointer;
}

.logged-in {
  color: black;
  align-self: center;
}

.logged-in a {
  color: black;
  font-size: 16px;
}

.logged-in i {
  color: black;
  padding: 0 6px;
}

.ant-modal-body {
  color: black;
  padding: 20px 55px 40px 55px;
  margin-bottom: 20px;
}

@media only screen and (max-width: 767px) {
  .logo img {
    color: black;
    height: 30px;
  }

  .rhs-buttons button {
    color: black;
    margin-right: 10px;
  }

  .tag-block {
    color: black;
    display: none;
  }
  .ant-modal-body {
    color: black;
    padding: 20px 20px 40px 20px;
  }
}



